@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;500;700&display=swap');

:root{

    --light-cyan: hsl(153, 71%, 59%);
    --black: hsl(0, 0%, 8%);
    --dark-grey: #242424; 
    --light-grey: hsl(0, 0%, 85%);
    --white: hsl(0, 0%, 100%);
}

html{
    font-size:62.5%;
    box-sizing: border-box;
}
*{
    margin:0;
    padding:0;
    font-family:"Space Grotesk", sans-serif;
}
.App{

    padding:2rem 2rem;
    background-color: var(--black);
    height:100%;
    width:auto;
}
main{
    height:100%;
    background-color: var(--black);
    width:111.1rem;
}

/* TEXT SETTINGS */
.black{
    color:var(--black);
}
.white{
    color:var(--white);
}
.light-grey{
    color:var(--light-grey);
    opacity:50%;
}
.dark-grey{
    color:var(--dark-grey);
}
.light-cyan{
    color:var(--light-cyan);
}
.text-center{
  text-align:center;
}
p{
    font-size:1.8rem;
    color:var(--white);
    opacity:80%;
}
.heading-large{
    font-size:8.8rem;
    color:var(--white);
}
.heading-medium{
    font-size:4.8rem;
    color:var(--white);
    text-align: center;
   
}
.heading-small{
    font-size:3.4rem;
    color:var(--white);
    text-align: center;
    opacity:100%;
}

.fs-4{
    font-size:4.8rem;
}
a{
    margin:1rem 0rem;
    color:white;
    text-decoration:none;
    font-size:1.8rem;
    border-bottom:2px solid var(--light-cyan);
    letter-spacing: 3px;
    padding-bottom:1rem;
    text-transform:uppercase;
    font-weight:bold;
    cursor:pointer;
}
a:hover{
    color:var(--light-cyan);
}

/* DIV SETTINGS */

.bg-light-grey{
    background-color:var(--light-grey);
}
.bg-dark-grey{
    background-color:var(--dark-grey);
}
.bg-light-cyan{
    background-color:var(--light-cyan);
}
.bg-white{
    background-color: var(--white);
}
.border{
    border:1px solid white;
}
.card{
    padding:4rem 8rem;
    font-weight:bold;
}
/* LAYOUT SETTINGS */

.flex{
    display:flex;
}
.column{
    flex-direction:column;
}
.align-center{
    align-items: center;
}
.justify-right{
    justify-content:right;
}
.justify-center{
  justify-content: center;
}
.space-between{
    justify-content: space-between;
}
.gap-2{
    gap:2rem;
}
.gap-3{
  gap:3rem;
}
.p-2{
    padding:2rem;
}
.p-4{
    padding:4rem;
}
.margin-top-bottom-4{
    margin:4rem 0rem;
}
.margin-top-bottom-2{
  margin:2rem 0rem;
}
.width-full{
    width:100%;
}
.text-align-left{
    text-align: center;
}
/*PAGE SETTINGS */

.main-container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
    "headings text";
    width:111.1rem;
    height:34.8rem;
    gap: 2rem;
}
.headings{
    grid-area:headings;
}
.text{
    grid-area:text;
    display: flex;
    flex-direction:column;
    align-items: flex-end;
    gap:2rem;
}

.interactive-container{
    justify-content: space-between;
}

.contact-container{
    height:43.5rem;
    width:54rem;
    display:flex;
  flex-direction:column;
  align-items:center;
  justify-content: center;
  gap:4rem;
  background-color:var(--dark-grey);
  margin-bottom:5rem;
}
.form-container{
    height:43.5rem;
    width:54rem;
    display:flex;
  flex-direction:column;
  align-items:center;
  gap:2rem;
  justify-content: center;
    background-color:var(--dark-grey);
}

/* INPUT SETTINGS */

textarea{
    font-family:"Space Grotesk", sans-serif;
    

}
input{
    text-transform: uppercase;
}
.input{
    width:90%;
}
input, textarea{
    width:90%;
    background-color: inherit;
    border:none;
    border-bottom:1px solid white;
    outline:none;
    padding:2rem;
    color:white;
}
input:focus, textarea:focus{
    border-bottom:1px solid var(--light-cyan);
    outline:none;
}

/* OTHER SETTINGS */

.title{
  text-align: center;
  color:var(--white);
  font-weight:bold;
  font-size:3rem;
  opacity:100%;
  margin:2rem 0rem;
}

