        width:54rem;
    }

    #contact-container{
        margin-top:10rem;
        display:grid;
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 
        "text input"
        ". send";
        grid-row-gap:2rem;
        margin-bottom:5rem;
    }
    .placeholder{
        grid-area:input;
    }
    #send{
        grid-area:send;
    }
    .hidden-on-desktop{
        display:none;
    }
    h1{
        font-size:8rem;
    }

    .project-container {
        position: relative;
        display: inline-block;
      }
      
      .project-container img {
        opacity: 1;
        transition: opacity 0.3s ease;
      }
      
      .project-container:hover img {
        opacity: 0.3;
      }
      
      /* Add the top link overlay in the middle using the ::before pseudo-element */
      .link-overlay-top {
        display:block;
        content: ""; /* Remove content for the ::before pseudo-element */
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        font-size: 1.8rem;
        font-weight: bold;
        opacity: 0;
        text-decoration: none; /* Remove underline from the link text */
        text-transform:uppercase;
        cursor:pointer;
        
      }
      
      /* Add the bottom link overlay in the middle using the ::after pseudo-element */
      .link-overlay-bottom {
        display:block;
        content: ""; /* Remove content for the ::after pseudo-element */
        position: absolute;
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        font-size: 1.8rem;
        font-weight: bold;
        opacity: 0;
        text-decoration: none; /* Remove underline from the link text */
        text-transform: uppercase;
        cursor:pointer;
      }
      
      /* Show the link overlays on hover */
      .project-container:hover .link-overlay-top,
      .project-container:hover .link-overlay-bottom {
        opacity: 1;
      }      
      

}

.custom-dialog-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .custom-dialog {
    background-color: var(--dark-grey);
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    animation: fadeIn 0.3s ease;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  